


















































import {
  Component,
  Vue,
  Prop,
  Watch
} from "vue-property-decorator";
import bcdata from "../assets/data";
import NavMenu from '@/components/NavMenu.vue';
import VueQrcode from '@chenfengyuan/vue-qrcode';
import {
  ArchItem
} from '../model/data-model';

@Component({
  components: {
    NavMenu,
    [VueQrcode.name]: VueQrcode,
  }
})
export default class A4Paper extends Vue {

  private archData = bcdata.archData;
  private typeData = bcdata.typeData;
  private ecoData = bcdata.ecoData;
  private riskData = bcdata.riskData;
  private bcArchData: ArchItem[] = [];
  private testData: any;
  private type: "sticker" | "red" | "blue" | "helper" | "binary" | null = null;

  constructor() {
    super();
  }

  beforeMount() {
    this.update();
  }

  @Watch("$route")
  routeChanged(to: any, from: any) {
    this.update();
  }

  update() {
    this.type = < any > this.$route.params.id;
    if (this.type == "sticker") {
      this.bcArchData = bcdata.archList.sort((a, b) => a.id - b.id);
      this.bcArchData.splice(20);
    } else {
      if (this.type == "binary") {
        this.testData = [{
            content: '1',
            color: '#e850a8',
            class: 'bignumber',
          },
          {
            content: '0',
            color: '#66ccff',
            class: 'bignumber',
          },
        ];
      } else {
        this.testData = [{
            content: '',
            backColor: '#66ccff',
            class: 'block',
          },
          {
            content: '',
            backColor: '#e850a8',
            class: 'block',
          },
          {
            content: '',
            backColor: '#66cc33',
            class: 'block',
          },
          {
            content: '',
            backColor: 'black',
            class: 'block',
          },
        ];

        if (this.type == "red") {
          this.testData = [this.testData[1]];
        } else if (this.type == "blue") {
          this.testData = [this.testData[0]];
        } else if (this.type == "helper") {
          this.testData = Array(15).fill(this.testData[0])
            .concat(Array(15).fill(this.testData[1]))
            .concat(Array(25).fill(this.testData[2]))
            .concat(Array(5).fill(this.testData[3]));
        }
      }
    }
  }
}
